import * as React from 'react';
import {styled, alpha} from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import Badge from '@mui/material/Badge';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import AccountCircle from '@mui/icons-material/AccountCircle';
import MailIcon from '@mui/icons-material/Mail';
import NotificationsIcon from '@mui/icons-material/Notifications';
import MoreIcon from '@mui/icons-material/MoreVert';
import {Task} from "@mui/icons-material";
import {Stack, Switch, Tooltip} from "@mui/material";
import {useDispatchApp, useSelectorApp} from "../App";
import {actions} from "../Redux/Todo/ToDoReducer";
import {thunkAuth} from "../Redux/auth/Auth";
import {Navigate, NavLink} from 'react-router-dom';

const Search = styled('div')(({theme}) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
        marginLeft: theme.spacing(3),
        width: 'auto',
    },
}));

const SearchIconWrapper = styled('div')(({theme}) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({theme}) => ({
    color: 'inherit',
    '& .MuiInputBase-input': {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)})`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '20ch',
        },
    },
}));

export const PrimarySearchAppBar = React.memo(function PrimarySearchAppBar() {

        const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);

        const [mobileMoreAnchorEl, setMobileMoreAnchorEl] =
            React.useState<null | HTMLElement>(null);

        const state = useSelectorApp(state => state.toDoReducer)
        const isAuthorized = useSelectorApp(state => state.authReducer.isAuthorized)

        const dispatch = useDispatchApp()

        const isMenuOpen = Boolean(anchorEl);
        const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

        const handleProfileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {

            setAnchorEl(event.currentTarget);
        };

        const handleMobileMenuClose = () => {
            setMobileMoreAnchorEl(null);
        };

        const handleMenuClose = () => {
            setAnchorEl(null);
            handleMobileMenuClose();
        };

        const handleMobileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
            setMobileMoreAnchorEl(event.currentTarget);
        };

        const onOfflineMode = () => {
            dispatch(actions.changeUnauthorizedMode(!state.offlineMode))
        }
        const logout = () => {
          dispatch(thunkAuth.logout())
            handleMenuClose()

        }

        const offlineTitle = "B Offline режиме ваши данные будут сохранены в локальном хранилище, но не синхронизированы с сервером"
        const onlineTitle = 'В Online режиме ваши данные будут синхронизированы с сервером'

        const menuId = 'primary-search-account-menu';
        const renderMenu = (
            <Menu
                anchorEl={anchorEl}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                id={menuId}
                keepMounted
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                open={isMenuOpen}
                onClose={handleMenuClose}
            >
                <MenuItem onClick={handleMenuClose}>
                    Profile
                </MenuItem>

                <MenuItem onClick={handleMobileMenuOpen}>
                    My account
                </MenuItem>

                {
                    isAuthorized
                        ?
                        <MenuItem onClick={logout}>logout</MenuItem>
                        :
                        <NavLink  style={{textDecoration:'none', color:'inherit'}} to='/login'>
                            <MenuItem onClick={handleMenuClose}>login</MenuItem>
                        </NavLink>
                }


            </Menu>
        );

        const mobileMenuId = 'primary-search-account-menu-mobile';
        const renderMobileMenu = (
            <Menu
                anchorEl={mobileMoreAnchorEl}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                id={mobileMenuId}
                keepMounted
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                open={isMobileMenuOpen}
                onClose={handleMobileMenuClose}
            >
                <MenuItem>
                    <IconButton size="large" aria-label="show 4 new mails" color="inherit">
                        <Badge
                            badgeContent={50}
                            color="error">
                            <MailIcon/>
                        </Badge>
                    </IconButton>
                    <p>Messages</p>
                </MenuItem>
                <MenuItem>
                    <IconButton
                        size="large"
                        aria-label="show 17 new notifications"
                        color="inherit"
                    >
                        <Badge badgeContent={17} color="error">
                            <NotificationsIcon/>
                        </Badge>
                    </IconButton>
                    <p>Notifications</p>
                </MenuItem>
                <MenuItem onClick={handleProfileMenuOpen}>
                    <IconButton
                        size="large"
                        aria-label="account of current user"
                        aria-controls="primary-search-account-menu"
                        aria-haspopup="true"
                        color="inherit"
                    >
                        <AccountCircle/>
                    </IconButton>
                    <p>Profile</p>
                </MenuItem>
            </Menu>
        );

        return (
            <Box sx={{flexGrow: 1}}>
                <AppBar position="static">
                    <Toolbar>
                        <IconButton
                            size="large"
                            edge="start"
                            color="inherit"
                            aria-label="open drawer"
                            sx={{mr: 2}}
                        >
                            <MenuIcon/>
                        </IconButton>
                        <Typography
                            variant="h6"
                            noWrap
                            component="div"
                            sx={{display: {xs: 'none', sm: 'block'}}}
                        >
                            Todo
                        </Typography>
                        <Tooltip title={state.offlineMode?offlineTitle:onlineTitle}
                                 placement='bottom' arrow>
                            <Stack direction={'row'} alignItems={'center'}>
                                <Switch
                                    checked={state.offlineMode}
                                    onChange={onOfflineMode}
                                    inputProps={{'aria-label': 'controlled'}}
                                    color="error"
                                />
                                <Typography>{state.offlineMode ? 'Offline mode' : 'Online mode'}</Typography>
                            </Stack>
                        </Tooltip>
                        {/*<Search >*/}
                        {/*    <SearchIconWrapper>*/}
                        {/*        <SearchIcon />*/}
                        {/*    </SearchIconWrapper>*/}
                        {/*    <StyledInputBase*/}

                        {/*        placeholder="Search…"*/}
                        {/*        inputProps={{ 'aria-label': 'search' }}*/}
                        {/*    />*/}
                        {/*</Search>*/}
                        <Box sx={{flexGrow: 1}}/>
                        <Box sx={{display: {xs: 'none', md: 'flex'}}}>
                            <IconButton size="large" aria-label="show 5 new mails" color="inherit">
                                <Badge
                                    badgeContent={state.tasksTitle.length}
                                    color="error">
                                    <Task/>

                                </Badge>
                            </IconButton>
                            {/*<IconButton*/}
                            {/*    size="large"*/}
                            {/*    aria-label="show 17 new notifications"*/}
                            {/*    color="inherit"*/}
                            {/*>*/}
                            {/*    <Badge badgeContent={17} color="error">*/}
                            {/*        <NotificationsIcon />*/}
                            {/*    </Badge>*/}
                            {/*</IconButton>*/}
                            <IconButton
                                size="large"
                                edge="end"
                                aria-label="account of current user"
                                aria-controls={menuId}
                                aria-haspopup="true"
                                onClick={handleProfileMenuOpen}
                                color="inherit"
                            >
                                <AccountCircle />
                            </IconButton>
                        </Box>
                        <Box sx={{display: {xs: 'flex',/* md: 'none'*/}}}>
                            <IconButton
                                size="large"
                                aria-label="show more"
                                aria-controls={mobileMenuId}
                                aria-haspopup="true"
                                onClick={handleMobileMenuOpen}
                                color="inherit"
                            >
                                <MoreIcon/>
                            </IconButton>
                        </Box>
                    </Toolbar>
                </AppBar>
                {renderMobileMenu}
                {renderMenu}
            </Box>
        );
    }
)
